<template>
  <div class="dashboard">
    <h1>{{ msg }}</h1>
    <div v-if="userExists">
      Welcome {{ pseudo }}. Destroy your account by clicking <a href="#" @click="destroyAccount">here</a>.
    </div>
    <div v-else>Sign up <router-link to="/signup">here</router-link>.</div>
    <div>
      <h4>Winner: {{winner}}</h4>
      <h4>You already tried {{guessTime}} times.</h4>
      Guess a number between 1 - 100 : <input v-model.number="guessNum" />
      <button @click.prevent="guess">Guess</button>
    </div>
  </div>
</template>

<script>
import Users from '@/js/users'
import Game from '@/js/game'

export default {
  name: 'dashboard',
  data () {
    return {
      msg: 'Welcome to your truffle-vue dApp',
      pseudo: undefined,
      winner: '',
      guessTime: 0,
      isWaiting: false,
      guessNum: 0,
      timeInterval: null
    }
  },
  computed: {
    userExists: function () {
      return (typeof this.pseudo !== 'undefined')
    }
  },
  beforeCreate: function () {
    Users.init().then(() => {
      Users.exists(window.web3.eth.accounts[0]).then((exists) => {
        if (exists) {
          Users.authenticate().then(pseudo => {
            this.pseudo = pseudo
          })
        }
      })
    }).catch(err => {
      console.log(err)
    })

    Game.init().then(() => {
      this.getWinner()
      this.getGuessTime()
      Game.getAnswer(window.web3.eth.accounts[0]).then((answer) => {
        console.log(answer.toNumber())
      })
    }).catch(err => {
      console.log(err)
    })
  },
  mounted: function () {
    this.timeInterval = setInterval(
      this.getGuessTime,
      500
    )
  },
  watch: {
    guessTime: function (val) {
      this.isWaiting = false
    }
  },
  beforeDestroy: function () {
    clearInterval(this.timeInterval)
  },
  methods: {
    destroyAccount: function (e) {
      e.preventDefault()
      Users.destroy().then(() => {
        this.pseudo = undefined
      }).catch(err => {
        console.log(err)
      })
    },
    getWinner: function () {
      Game.getWinner(window.web3.eth.accounts[0]).then((winner) => {
        this.winner = winner
      })
    },
    getGuessTime: function () {
      Game.getGuessTime(window.web3.eth.accounts[0]).then((guessTime) => {
        this.guessTime = guessTime.toNumber()
      })
    },
    guess: function () {
      if (this.isWaiting) return

      Game.guess(this.guessNum).then((response) => {
        this.isWaiting = true
        console.log(response)
        this.getWinner()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  display: block;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



