import contract from 'truffle-contract'
import GameContract from '@contracts/Game.json'

const Game = {

  contract: null,

  instance: null,

  init: function () {
    let self = this

    return new Promise(function (resolve, reject) {
      self.contract = contract(GameContract)
      self.contract.setProvider(window.web3.currentProvider)

      self.contract.deployed().then(instance => {
        self.instance = instance
        resolve()
      }).catch(err => {
        reject(err)
      })
    })
  },

  guess: function (guessNumber) {
    let self = this

    return new Promise((resolve, reject) => {
      self.instance.guess(
        guessNumber,
        {from: window.web3.eth.accounts[0]}
      ).then((correct) => {
        resolve(correct)
      }).catch(err => {
        reject(err)
      })
    })
  },

  getWinner: function () {
    let self = this

    return new Promise((resolve, reject) => {
      self.instance.getWinner(
        {from: window.web3.eth.accounts[0]}
      ).then(winner => {
        resolve(winner)
      }).catch(err => {
        reject(err)
      })
    })
  },

  getGuessTime: function () {
    let self = this

    return new Promise((resolve, reject) => {
      self.instance.getGuessTime(
        {from: window.web3.eth.accounts[0]}
      ).then(time => {
        resolve(time)
      }).catch(err => {
        reject(err)
      })
    })
  },

  getAnswer: function() {
    let self = this

    return new Promise((resolve, reject) => {
      self.instance.getAnswer(
        {from: window.web3.eth.accounts[0]}
      ).then(answer => {
        resolve(answer)
      }).catch(err => {
        reject(err)
      })
    })
  }
}

export default Game



